'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { toast } from 'sonner';
import { format } from 'date-fns';
import {
    Save,
    ArrowLeft,
    Loader2,
    Camera,
    UserCheck,
    UserX,
    ShieldHalf,
    CreditCard,
    Globe,
    Calendar,
    User
} from 'lucide-react';
import * as z from 'zod';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
    Form,
    FormControl,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
    FormDescription
} from '@/components/ui/form';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from '@/components/ui/select';
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Switch } from '@/components/ui/switch';
import { Separator } from '@/components/ui/separator';

// Logic & Types
import { api } from '@/lib/api';
import { jugadorSchema } from '@/lib/validations';
import type { Club } from '@/types';

// Función auxiliar para formatear RUT visualmente
const formatRut = (rut: string) => {
    const clean = rut.replace(/[^0-9kK]/g, '');
    if (clean.length <= 1) return clean;
    const body = clean.slice(0, -1);
    const dv = clean.slice(-1).toUpperCase();
    const formattedBody = body.replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    return `${formattedBody}-${dv}`;
};

export default function NuevoJugadorPage() {
    const router = useRouter();
    const [clubes, setClubes] = useState<Club[]>([]);
    const [isLoadingClubs, setIsLoadingClubs] = useState(true);

    // 1. Configuración del Formulario
    const form = useForm({
        resolver: zodResolver(jugadorSchema),
        defaultValues: {
            tipo_identificacion: 'RUT' as const,
            nombres: '',
            paterno: '',
            materno: '',
            rut: '',
            passport: '',
            nacionalidad: 'Chilena',
            delegado: '',
            club_id: '',
            nacimiento: '',
            inscripcion: format(new Date(), 'yyyy-MM-dd'),
            activo: true,
            foto: undefined,
        },
    });

    const tipoIdentificacion = form.watch('tipo_identificacion');
    const isActivo = form.watch('activo'); // Observamos el estado para cambiar el color

    // 2. Cargar Clubes
    useEffect(() => {
        async function loadClubes() {
            try {
                const data = await api.getClubes();
                setClubes(data);
            } catch (error) {
                toast.error('Error al cargar la lista de clubes');
            } finally {
                setIsLoadingClubs(false);
            }
        }
        loadClubes();
    }, []);

    // 3. Submit
    async function onSubmit(data: z.infer<typeof jugadorSchema>) {
        try {
            const payload: any = {
                ...data,
                rut: data.rut || '',
                materno: data.materno || '',
                passport: data.passport || '',
                club_id: data.club_id,
                nacionalidad: data.nacionalidad,
                delegado: data.delegado,
                tipo_identificacion_input: data.tipo_identificacion,
                passport_input: data.passport,
                activo: data.activo,
                foto: data.foto
            };

            await api.createJugador(payload);

            toast.success('Jugador inscrito correctamente');
            router.push('/jugadores');
        } catch (error) {
            console.error(error);
            toast.error('Error al crear el jugador. Revise los datos.');
        }
    }

    return (
        <div className="max-w-4xl mx-auto py-8 px-4 space-y-6">

            {/* Header de la página */}
            <div className="flex items-center gap-4 mb-8">
                <Button variant="outline" size="icon" onClick={() => router.back()} type="button" className="h-10 w-10 rounded-full border-slate-300">
                    <ArrowLeft className="h-5 w-5 text-slate-600" />
                </Button>
                <div>
                    <h1 className="text-3xl font-bold tracking-tight text-slate-900 dark:text-white">Inscribir jugador</h1>
                    <p className="text-slate-500 dark:text-slate-400">Complete la ficha técnica para registrar un nuevo jugador en la asociación.</p>
                </div>
            </div>

            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">

                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">

                        {/* COLUMNA IZQUIERDA: Foto y Estado */}
                        <div className="lg:col-span- space-y-6">
                            <Card className="border-slate-200 shadow-sm overflow-hidden">
                                <CardHeader className="bg-slate-50 border-b border-slate-100 pb-4">
                                    <CardTitle className="text-base font-semibold text-slate-800 flex items-center gap-2">
                                        <Camera className="h-4 w-4 text-blue-600" />
                                        Perfil y Estado
                                    </CardTitle>
                                </CardHeader>
                                <CardContent className="p-6 space-y-6">
                                    {/* FOTO */}
                                    <FormField
                                        control={form.control}
                                        name="foto"
                                        render={({ field: { value, onChange, ...field } }) => (
                                            <FormItem>
                                                <div className="flex flex-col items-center justify-center gap-4">
                                                    <div className="h-32 w-32 rounded-full bg-slate-100 border-2 border-dashed border-slate-300 flex items-center justify-center overflow-hidden relative group cursor-pointer hover:border-blue-400 transition-colors">
                                                        {value ? (
                                                            <div className="text-xs text-center p-2 text-slate-500">Imagen cargada</div>
                                                        ) : (
                                                            <Camera className="h-8 w-8 text-slate-300" />
                                                        )}
                                                        <Input
                                                            {...field}
                                                            type="file"
                                                            accept="image/*"
                                                            className="absolute inset-0 opacity-0 cursor-pointer"
                                                            onChange={(e) => {
                                                                const file = e.target.files?.[0];
                                                                if (file) onChange(file);
                                                            }}
                                                        />
                                                    </div>
                                                    <FormLabel className="text-blue-600 cursor-pointer hover:underline text-sm font-medium">
                                                        Subir Fotografía
                                                    </FormLabel>
                                                    <FormDescription className="text-xs text-center">
                                                        Recomendado: 500x500px <br /> JPG o PNG
                                                    </FormDescription>
                                                </div>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />

                                    <Separator />

                                    {/* ACTIVO - SWITCH PERSONALIZADO */}
                                    <FormField
                                        control={form.control}
                                        name="activo"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="mb-2 block text-sm font-semibold text-slate-700">Estado Federativo</FormLabel>
                                                <FormControl>
                                                    <div
                                                        className={`
                                                            flex items-center justify-between p-3 rounded-lg border transition-all duration-300
                                                            ${field.value
                                                                ? 'bg-green-50 border-green-200 dark:bg-green-900/20 dark:border-green-800'
                                                                : 'bg-red-50 border-red-200 dark:bg-red-900/20 dark:border-red-800'}
                                                        `}
                                                    >
                                                        <div className="flex items-center gap-3">
                                                            <div className={`p-2 rounded-full ${field.value ? 'bg-green-100 text-green-700' : 'bg-red-100 text-red-700'}`}>
                                                                {field.value ? <UserCheck className="h-4 w-4" /> : <UserX className="h-4 w-4" />}
                                                            </div>
                                                            <div className="flex flex-col">
                                                                <span className={`text-sm font-bold ${field.value ? 'text-green-800' : 'text-red-800'}`}>
                                                                    {field.value ? 'Habilitado' : 'Suspendido'}
                                                                </span>
                                                                <span className={`text-xs ${field.value ? 'text-green-600' : 'text-red-600'}`}>
                                                                    {field.value ? 'Puede jugar partidos' : 'No puede ser alineado'}
                                                                </span>
                                                            </div>
                                                        </div>

                                                        {/* Aquí está el truco para cambiar el color del Switch */}
                                                        <Switch
                                                            checked={field.value}
                                                            onCheckedChange={field.onChange}
                                                            className={`${field.value ? 'bg-green-600 hover:bg-green-700' : 'bg-red-500 hover:bg-red-600'} border-2 border-transparent data-[state=unchecked]:bg-red-500`}
                                                        />
                                                    </div>
                                                </FormControl>
                                            </FormItem>
                                        )}
                                    />
                                </CardContent>
                            </Card>
                        </div>

                        {/* COLUMNA DERECHA: Datos del Formulario */}
                        <div className="lg:col-span-2 space-y-6">

                            {/* TARJETA 1: Identificación y Nombres */}
                            <Card className="border-slate-200 shadow-sm">
                                <CardHeader className="bg-blue-50/50 border-b border-blue-100 pb-4">
                                    <CardTitle className="text-base font-semibold text-blue-900 flex items-center gap-2">
                                        <User className="h-4 w-4" />
                                        Datos Personales
                                    </CardTitle>
                                </CardHeader>
                                <CardContent className="p-6 space-y-6">
                                    {/* Tipo ID y Numero */}
                                    <div className="grid grid-cols-1 md:grid-cols-12 gap-6">
                                        <div className="md:col-span-5">
                                            <FormField
                                                control={form.control}
                                                name="tipo_identificacion"
                                                render={({ field }) => (
                                                    <FormItem className="space-y-2">
                                                        <FormLabel className="text-slate-600 text-xs uppercase font-bold tracking-wider">Documento</FormLabel>
                                                        <FormControl>
                                                            <RadioGroup
                                                                onValueChange={field.onChange}
                                                                defaultValue={field.value}
                                                                className="flex flex-col space-y-2"
                                                            >
                                                                <FormItem className="flex items-center space-x-3 space-y-0 rounded-md border p-3 hover:bg-slate-50 cursor-pointer">
                                                                    <FormControl>
                                                                        <RadioGroupItem value="RUT" />
                                                                    </FormControl>
                                                                    <div className="flex items-center gap-2">
                                                                        <CreditCard className="h-4 w-4 text-slate-400" />
                                                                        <FormLabel className="font-normal cursor-pointer">RUT Chileno</FormLabel>
                                                                    </div>
                                                                </FormItem>
                                                                <FormItem className="flex items-center space-x-3 space-y-0 rounded-md border p-3 hover:bg-slate-50 cursor-pointer">
                                                                    <FormControl>
                                                                        <RadioGroupItem value="PASSPORT" />
                                                                    </FormControl>
                                                                    <div className="flex items-center gap-2">
                                                                        <Globe className="h-4 w-4 text-slate-400" />
                                                                        <FormLabel className="font-normal cursor-pointer">Pasaporte</FormLabel>
                                                                    </div>
                                                                </FormItem>
                                                            </RadioGroup>
                                                        </FormControl>
                                                    </FormItem>
                                                )}
                                            />
                                        </div>

                                        <div className="md:col-span-7 flex flex-col justify-end">
                                            {tipoIdentificacion === 'RUT' ? (
                                                <FormField
                                                    control={form.control}
                                                    name="rut"
                                                    render={({ field }) => (
                                                        <FormItem>
                                                            <FormLabel>Número de RUT</FormLabel>
                                                            <FormControl>
                                                                <div className="relative">
                                                                    <CreditCard className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                                    <Input
                                                                        placeholder="12.345.678-9"
                                                                        className="pl-9 font-mono bg-slate-50 border-slate-200 focus-visible:ring-blue-500"
                                                                        {...field}
                                                                        value={field.value as string || ''}
                                                                        onChange={(e) => {
                                                                            const formatted = formatRut(e.target.value);
                                                                            if (formatted.length <= 12) field.onChange(formatted);
                                                                        }}
                                                                    />
                                                                </div>
                                                            </FormControl>
                                                            <FormMessage />
                                                        </FormItem>
                                                    )}
                                                />
                                            ) : (
                                                <FormField
                                                    control={form.control}
                                                    name="passport"
                                                    render={({ field }) => (
                                                        <FormItem>
                                                            <FormLabel>N° Pasaporte</FormLabel>
                                                            <FormControl>
                                                                <div className="relative">
                                                                    <Globe className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                                    <Input
                                                                        placeholder="A12345678"
                                                                        className="pl-9 font-mono bg-slate-50 border-slate-200"
                                                                        {...field}
                                                                        value={field.value as string || ''}
                                                                    />
                                                                </div>
                                                            </FormControl>
                                                            <FormMessage />
                                                        </FormItem>
                                                    )}
                                                />
                                            )}
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                        <FormField
                                            control={form.control}
                                            name="nombres"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel>Nombres</FormLabel>
                                                    <FormControl>
                                                        <Input placeholder="Ej: Juan Andrés" {...field} value={field.value as string || ''} />
                                                    </FormControl>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />
                                        <div className="grid grid-cols-2 gap-2">
                                            <FormField
                                                control={form.control}
                                                name="paterno"
                                                render={({ field }) => (
                                                    <FormItem>
                                                        <FormLabel>Ap. Paterno</FormLabel>
                                                        <FormControl>
                                                            <Input placeholder="Pérez" {...field} value={field.value as string || ''} />
                                                        </FormControl>
                                                        <FormMessage />
                                                    </FormItem>
                                                )}
                                            />
                                            <FormField
                                                control={form.control}
                                                name="materno"
                                                render={({ field }) => (
                                                    <FormItem>
                                                        <FormLabel>Ap. Materno</FormLabel>
                                                        <FormControl>
                                                            <Input placeholder="González" {...field} value={field.value as string || ''} />
                                                        </FormControl>
                                                        <FormMessage />
                                                    </FormItem>
                                                )}
                                            />
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                        <FormField
                                            control={form.control}
                                            name="nacimiento"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel>Fecha Nacimiento</FormLabel>
                                                    <FormControl>
                                                        <div className="relative">
                                                            <Calendar className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                                                            <Input type="date" className="pl-9" {...field} value={field.value as string || ''} />
                                                        </div>
                                                    </FormControl>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />
                                        <FormField
                                            control={form.control}
                                            name="nacionalidad"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel>Nacionalidad</FormLabel>
                                                    <FormControl>
                                                        <Input placeholder="Ej: Chilena" {...field} value={field.value as string || ''} />
                                                    </FormControl>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />
                                    </div>
                                </CardContent>
                            </Card>

                            {/* TARJETA 2: Datos Deportivos */}
                            <Card className="border-slate-200 shadow-sm">
                                <CardHeader className="bg-indigo-50/50 border-b border-indigo-100 pb-4">
                                    <CardTitle className="text-base font-semibold text-indigo-900 flex items-center gap-2">
                                        <ShieldHalf className="h-4 w-4" />
                                        Ficha Deportiva
                                    </CardTitle>
                                </CardHeader>
                                <CardContent className="p-6 space-y-6">
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                        <FormField
                                            control={form.control}
                                            name="club_id"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel>Club de Pertenencia</FormLabel>
                                                    <Select onValueChange={field.onChange} defaultValue={field.value as string}>
                                                        <FormControl>
                                                            <SelectTrigger className="bg-white border-slate-200 focus:ring-indigo-500">
                                                                <SelectValue placeholder={isLoadingClubs ? "Cargando..." : "Seleccione un club"} />
                                                            </SelectTrigger>
                                                        </FormControl>
                                                        <SelectContent>
                                                            {clubes.map((club) => (
                                                                <SelectItem key={club.id} value={club.id.toString()}>
                                                                    {club.nombre}
                                                                </SelectItem>
                                                            ))}
                                                        </SelectContent>
                                                    </Select>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />

                                        <FormField
                                            control={form.control}
                                            name="inscripcion"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel>Fecha de Inscripción</FormLabel>
                                                    <FormControl>
                                                        <Input type="date" {...field} value={field.value as string || ''} />
                                                    </FormControl>
                                                    <FormMessage />
                                                </FormItem>
                                            )}
                                        />
                                    </div>

                                    <FormField
                                        control={form.control}
                                        name="delegado"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel>Delegado Responsable</FormLabel>
                                                <FormControl>
                                                    <Input placeholder="Nombre del oficial que inscribe" {...field} value={field.value as string || ''} />
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                </CardContent>
                            </Card>

                        </div>
                    </div>

                    {/* Footer con Botones */}
                    <div className="flex justify-end gap-4 pt-4 border-t border-slate-200">
                        <Button type="button" variant="ghost" onClick={() => router.back()} className="text-slate-500 hover:text-slate-700 hover:bg-slate-100">
                            Cancelar Operación
                        </Button>
                        <Button
                            type="submit"
                            disabled={form.formState.isSubmitting}
                            className="bg-blue-600 hover:bg-blue-700 text-white min-w-[150px] shadow-sm shadow-blue-200"
                        >
                            {form.formState.isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
                            Registrar Jugador
                        </Button>
                    </div>
                </form>
            </Form>
        </div>
    );
}